.preview-frame {
    margin: 0 auto 64px;
    position: relative;
    width: min(90vw, 520px);
    max-height: 70vh;
    aspect-ratio: 2 / 3;
    background-color: #e7e7e7;
    overflow: hidden;
    border-radius: 12px;
}
.frame-slot{
    width: 40.9%;
    height: 41.5%;
    position: absolute;
    overflow: hidden;
    z-index: 1;
    border-radius: 10px;
}

.frame-slot img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.1);
    transform-origin: center center;
}

.slot-0 {
    top: 4.4%;
    left: 6.6%;
}

.slot-1 {
    top: 4.4%;
    left: 52.3%;
}

.slot-2 {
    top: 46.3%;
    left: 6.6%;
}

.slot-3 {
    top: 46.3%;
    left: 52.3%;
}

.frame-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    z-index: 2;
}
